import Vue from 'vue'
import Router from 'vue-router'

import Specials from './views/Specials.vue'
import About from './views/About.vue'
import Menu from './views/Menu.vue'
import Chef from './views/Chef.vue'
import Contact from './views/Contact.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'specials',
      component: Specials
    },
    {
      path: '/about',
      name: 'about',
      component : About
    },
    {
      path: '/menu',
      name: 'menu',
      component : Menu
    },
    {
      path: '/chef',
      name: 'chef',
      component : Chef
    },
    {
      path: '/contact',
      name: 'contact',
      component : Contact
    }
  ]
})
